	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, sans-serif;
	background: linear-gradient(to bottom, #061d3b, #29265d);
}
@font-face {
	font-family: "sinkin_sans";
	src:
		url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"),
		url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "source_sans";
	src:
		url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format("woff2"),
		url(${___CSS_LOADER_URL_REPLACEMENT_3___}) format("woff");
	font-weight: normal;
	font-style: normal;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 20px;
}

.header-bar {
	grid-column: 1/3;
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: space-between;
	align-items: center;
	background-color: #233167;
	color: rgba(255, 255, 255, 0.886);
	padding: 10px 20px;
	margin-left: 1.5rem;
	border-radius: 0.8rem;
}

.title {
	font-size: 24px;
	font-family: "sinkin_sans";
}

#search-field {
	display: flex;
	align-items: center;
}

#search {
	padding: 5px;
	border-radius: 5px;
	border: none;
	margin-right: 10px;
	background-color: #1d1e4a;
	color: #e4e4e4;
}
#search::placeholder {
	color: #e4e4e4;
	font-family: "source_sans";
}

.search-btn {
	font-family: "source_sans";
	padding: 5px 10px;
	border-radius: 5px;
	border: none;
	background-color: #231a4f;
	color: white;
	cursor: pointer;
	font-weight: 700;
}

.today-main {
	color: #eee;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-row: auto 1fr;
	justify-content: space-between;
	background-color: #333481;
	border-radius: 0.8rem;
	padding: 20px;
	margin: 1rem 2rem;
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}
.description {
	font-family: "source_sans";
	font-weight: 600;
	font-size: 1.2rem;
}

.today-main .icon img {
	width: 7rem;
}

.main-leftdata {
	grid-column: 1/2;
}
.main-rightdata {
	grid-column: 2/3;
	grid-row: 1/3;
	font-family: "source_sans";
	font-size: 1.1rem;
	font-weight: 700;
	padding-left: 1rem;
}

.city {
	font-family: "sinkin_sans";
	font-size: 1.7rem;
	font-weight: bold;
}

.temperature {
	font-family: "source_sans";
	font-size: 1.8rem;
	font-weight: 700;
	padding-bottom: 0.5rem;
}
.conditions {
	padding-bottom: 0.5rem;
	font-size: 1.2rem;
}
.seven-days-container {
	display: flex;
	flex-direction: column;
	border-radius: 1rem;
	background-color: #150d40;
	padding: 1rem;
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
	margin-bottom: 2rem;
}

.seven-day-title {
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 1.5rem;
	color: #eee;
	font-family: "sinkin_sans";
}

.seven-cards {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: space-between;
	margin-bottom: 2rem;
	padding: 10px;
	background-color: #243b63;
	border-radius: 0.5rem;
	color: #eee;
	font-weight: 700;
	transition: 0.4s background-color;
}
.seven-cards:hover {
	background-color: #233167;
}
#last-card {
	margin-bottom: 1rem;
}

.seven-icon {
	justify-self: center;
	align-self: center;
}
.seven-icon img {
	width: 4rem;
	grid-column: 1/2;
}
.seven-data {
	grid-column: 2/3;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.seven-data > .seven-day {
	font-family: "sinkin_sans";
	font-weight: 800;
	text-decoration: underline;
	padding-bottom: 2px;
}

.today-hours-container {
	padding: 0 2rem;
}

.today-cards-section {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(20rem, 0.8fr));
	gap: 1.5rem;
	align-items: stretch;
	margin-top: 0;
	margin-bottom: 20px;
	padding-top: 0;
}

.today-title {
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 10px;
	color: #e4e4e4;
	font-family: "sinkin_sans";
}

.today-hours-cards {
	display: grid;
	grid-template-columns: 1fr 1fr;
	position: relative;
	background-color: #202c61;
	color: rgb(37, 65, 109);
	text-align: left;
	min-height: calc(120px + 4vw);
	border-radius: 7px;
	box-shadow: 2px 2px 5px rgba(25, 5, 90, 0.671);
	transition: 0.3s background-color;
	cursor: pointer;
	padding: 1rem;
}
.today-hours-cards:hover {
	background-color: #333481;
}

.today-hours-icon img {
	width: 6rem;
	grid-column: 1/2;
}
.today-hours-icon {
	align-self: center;
	justify-self: center;
}
.hours-data {
	font-family: "source_sans";
	grid-column: 2/3;
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-size: 1.3rem;
	font-weight: 700;
	color: #eee;
}

.today-hours-time {
	font-size: 2rem;
}
@media (min-width: 768px) {
	.container {
		display: grid;
		grid-template-columns: 1fr 300px;
		grid-template-rows: auto 1fr;
		grid-gap: 20px;
	}

	.today-main {
		grid-column: 1 / 2;
	}

	.seven-days-container {
		margin-top: 1rem;
		grid-column: 2 / 3;
		grid-row: 2/4;
	}
	.today-hours-container {
		grid-column: 1/2;
		grid-row: 3/4;
	}

	.seven-cards,
	.today-hours-cards {
		justify-content: space-between;
	}
}

@media (max-width: 768px) {
	.container {
		display: block;
	}

	.today-main {
		display: block;
	}

	.seven-days-container,
	.today-hours-container {
		display: block;
	}
}
